<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process184"></a>

<p><br></p>
<p><span class="rvts17">埋葬过去（</span><span class="rvts50">Burying The Past</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:386,Y:275</span><span class="rvts21">，加雷斯（</span><span class="rvts20">Gareth</span><span class="rvts21">）第一次出现的位置</span>
<br><span class="rvts20">X:665,Y:265</span><span class="rvts21">，加雷斯（</span><span class="rvts20">Gareth</span><span class="rvts21">）第二次出现的位置</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助加雷斯处理他的事情</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1204.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">加雷斯第一次会在一处破房子里出现（</span><span class="rvts20">X:386,Y:275</span><span class="rvts21">）。</span>
<br><span class="rvts21">此时我们会发现，他之前忠实的手下遭到不明势力的袭击，加雷斯几近崩溃。可以试图把他劝走（让他回到复仇女神号），也可以尝试帮他报仇。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1205.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">加雷斯第二次出现在自己的农场，这次更惨。加雷斯的父母被谋害，加雷斯正在处理后事。与他发生对话后，把他劝回复仇女生号，这便是该支线的好的结局。</span>
<br><span class="rvts20"></span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1206.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">或帮他杀掉那些沉默的僧侣（在不远处加雷斯父母的房间里，门外有两个魔导师守卫把守）。之后加雷斯会闯进来，让你调查他父母被杀的事情。</span>
<br><span class="rvts21">进入房间后，记得使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">，与加雷斯的父母谈话，并获得他们被杀害的证据。</span>
<br><span class="rvts20"></span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1207.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">可以在屋外的一棵大树下找到白魔导师乔纳森（</span><span class="rvts20">Jonathan</span><span class="rvts21">）的手套。之后该任务将进入一段旁白，选择为加雷斯复仇，不管或放弃。</span>
<br><span class="rvts21">但这样做的最终结果将导致加雷斯在无名岛谋反亚历山大，他将永远离开我们的队伍，并在最终</span><span class="rvts20">BOSS</span><span class="rvts21">大战中成为黑暗的僧侣使者与我们对抗。因此可以看出，帮他复仇会是一个坏的结局。</span>
<br><span class="rvts21">还有这么一种可能：当劝阻加雷斯呆在船上并且之后杀掉了白魔导师乔纳森，那么加雷斯的父母就不会死亡，他们会在自家农场，并躲过了这场灾难。（需要在第一次遇到加雷斯的时候就劝他回去）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



